.selection {
  font: normal 20px 'Bitter', serif;
  color: #2A88AD;
  margin-bottom: 10px;
  margin-top: 15px;
}

.selection>span {
  background: #2A88AD;
  padding: 5px 10px 5px 10px;
  position: absolute;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border: 4px solid #fff;
  font-size: 14px;
  margin-left: -45px;
  color: #fff;
  margin-top: -3px;
}