.name {
  padding: 10px 30px 10px 10px;
  background: #efecec;

}

.name label {
  display: block;
  font-size: 12px;
  color: gray;
  font-weight: bold;
}

.name input,
textarea {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: aliceblue;
  font-size: 15px;
  outline: none;
  resize: vertical;
  border-width: 1px;
}


.error {
  color: red;
  font-size: 12px;
}